.navigation {
  display: block;
  position: relative;
  top: 35px;
  left: 36px;

  @include vp-767 {
    left: -40px;
  }

  @include vp-374 {
    flex-direction: column;
    gap: 20px;
    max-width: 90px;
    flex-wrap: wrap;
    top: 32px;
    left: -42px;
  }
}

.navigation__list {
  list-style: none;
  display: flex;
  gap: 40px;

  @include vp-767 {
    gap: 30px;
  }

  @include vp-374 {
    flex-direction: column;
    flex-wrap: wrap;
    max-height: 120px;
    gap: 23px;
  }
}

.navigation__link {
  color: #ffffff;
}

.navigation__item {
  @include input;

  line-height: 16px;
  text-align: left;

  @include vp-374 {
    @include h5;

    line-height: 19px;
    text-align: left;
    min-width: 135px;
  }
}

.navigation__item:nth-child(1) {
  padding-right: 130px;

  @include vp-767 {
    padding-right: 60px;
  }
}
